export function getCardColor(card: string) {
    let suit = card[0];
    if (suit == "X") {
        return "purple";
    } else if (suit == "H" || suit == "D") {
        return "red";
    } else {
        return "black";
    }
}

export function getCardChar(card: string) {
    return CARD_TO_CHAR[card];
}
